$font-size: 1.2em;
$line-height: 1.8em;
.content-offer {
    .header-text {
        cursor: default;
        margin: 80px auto 0 auto;
        padding: 0;
        text-align: center;
        @media (max-width: $screen-sm-min) {
            margin: 50px;
        }
        h2 {
            text-transform: uppercase;
        }
    }
    p {
        cursor: default;
        font-size: $font-size;
        line-height: $line-height;
    }
    .fa{
        font-size: 2em;
        opacity: 0.7;
        padding-right: 20px;
        transition: font-size 0.2s, opacity 0.2s ease;
        &:hover {
            font-size: 2.2em; opacity: 1;
        }
    }
    .fa-truck {
        color: #00aead;
    }
    .fa-graduation-cap {
        color: #ff6347;
    }
    .fa-leaf {
        color: #a2cd5a;
    }
    .fa-credit-card {
        color: #ffba00;
    }
    .fa-cogs {
        color: #963FD5;
    }
    .fa-television {
        color: #4e51d8;
    }

    .text-hidden {
        height: 90px;
        overflow: hidden;
    }
}

.hvr-border-fade {
    box-shadow: inset 0 0 0 2px rgba(195, 195, 195, 0.41),0 0 1px rgba(0,0,0,0);
    transition-property: box-shadow;
}

.info-text {
    margin: 40px 50px 80px 150px;
    @media (max-width: $screen-lg-min) {
        margin: 50px 50px 50px 100px;
    }
    @media (max-width: $screen-md-min) {
        margin: 50px;
    }
    @media (max-width: $screen-xs-min) {
        margin: 30px 10px;
    }
    &-2 {
        margin: 40px 150px 80px 50px;
        @media (max-width: $screen-lg-min) {
            margin: 50px 100px 50px 50px;
        }
        @media (max-width: $screen-md-min) {
            margin: 0 50px 50px 50px;
        }
        @media (max-width: $screen-xs-min) {
            margin: 30px 15px;
        }
    }
}

.btn {
    margin: 20px auto 0 auto;
    @media (max-width: $screen-sm-min) {
        margin: 40px auto;
    }
}

h4 {
    display: inline-block;
    margin-bottom: 20px;
    a {
        cursor: pointer;
        transition: all .2s ease-in-out;
    }
}

.content-skills {
    background: linear-gradient(90deg, rgba(236, 233, 230, 0.5) 10%, #fff 90%);
    p {
        cursor: default;
        font-size: $font-size;
        line-height: $line-height;
    }
    h2 {
        cursor: default;
        margin-bottom: 40px;
        text-align: center;
        text-transform: capitalize;
    }
    ul {
        font-size: $font-size;
        line-height: $line-height;
        list-style: none;
        padding-left: 20px;
    }
    li {
        border-left: 2px solid #dbdbdb;
        cursor: pointer;
        display: block;
        margin: 10px 0;
        padding-left: 10px;
        transition: border .2s ease;
        &:first-child:hover, &:first-child:focus {
            border-left: 6px solid #f5876e;
        }
        &:nth-child(2):hover, &:nth-child(2):focus{
            border-left: 6px solid #61a8dc;
        }
        &:nth-child(3):hover, &:nth-child(3):focus{
            border-left: 6px solid #8eBd40;
        }
        &:nth-child(4):hover, &:nth-child(4):focus{
            border-left: 6px solid #d8c86e;
        }
        &:nth-child(5):hover, &:nth-child(5):focus{
            border-left: 6px solid #988cc3;
        }
    }
    .progress {
        border-radius: 4px;
        box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.4);
        height: 25px;
        margin-bottom: 25px;
    }
    .progress-bar {
        background-color: #8eBd40;
        box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.8);
        font-size: $font-size;
        line-height: 25px;
        text-align: right;
        transition: all 5s ease;
        &:hover, &:focus {
            background-color: #486931;
        }
    }
}

.our-offices {
    background: url('../img/content/world-map.svg') center/cover fixed no-repeat;
    p {
        font-size: $font-size;
        line-height: $line-height;
    }
    .accord-offices {
        margin: 40px 300px;
        overflow: hidden;
        @media (max-width: $screen-lg-min) {
            margin: 40px 200px;
        }
        @media (max-width: $screen-md-min) {
            margin: 40px;
        }
        @media (max-width: $screen-xs-min) {
            margin: 30px 10px;
        }
    }
    .btn {
        height: 100%;
        margin: 10px auto;
        @media (max-width: $screen-xs-min) {
            font-size: 1em;
        }
    }
    .mg-center {
        margin: 0 auto 25px auto;
        width: 300px;
        a {
            cursor: pointer;
            text-decoration: none;
        }
        @media (max-width: 1500px) {
            width: 200px;
        }
        h5 {
            padding-right: 10px;
            &::after {
                content: "\203A";
                font-size: 20px;
                padding-left: 10px;
                transition: all .4s ease-in-out;
            }
            &:hover, &focus {
                &::after {
                    padding-left: 20px;
                }
            }
        }
    }
    .panel-title {
        width: 100%;
    }
    .panel-heading {
        padding: 10px 0;
    }
    .btn-accord {
        width: 100%;
    }
    h2 {
        cursor: default;
        margin: 40px 0 0 0;
        text-transform: capitalize;
    }
}

#anim-circle {
    margin: 0 100px 80px 100px;
    @media (max-width: $screen-xs-min) {
        margin: 0;
    }
    .circle {
        cursor: default;
        height: 200px;
        margin: 20px auto;
        width: 200px;
        strong {
            font-size: 3em;
            left: calc(50% - 25px);
            position: absolute;
            top: calc(50% - 30px);
        }
    }
    #circle1, #circle2, #circle3 {
        background-color: #fff;
        border-radius: 50%;
    }
    .circle-caption {
        background-color: #deb887;
        border-radius: 4px;
        cursor: default;
        font-size: 2em;
        line-height: 2em;
        opacity: .8;
        padding: 0 20px;
        position: absolute;
        transition: all .5s ease-in-out;
        @media (max-width: $screen-md-min) {
            font-size: 1.2em;
        }
            &:hover, &focus {
                opacity: 1;
            }
        p {
            margin: 0;
        }
        &-1 {
            bottom: 20px;
            left: calc(50% + 25px);
            @media (max-width: 1500px) {
                left: calc(42% + 25px);
            }
            @media (max-width: $screen-md-min) {
                left: calc(50% - 75px);
            }
            @media (max-width: $screen-md-min) {
                left: calc(50% - 50px);
            }
        }
        &-2 {
            bottom: 20px;
            left: calc(50% - 78px);
            @media (max-width: $screen-md-min) {
                left: calc(50% - 55px);
            }
        }
        &-3 {
            bottom: 20px;
            left: 50px;
            @media (max-width: 1500px) {
                left: -25px;
            }
            @media (max-width: $screen-md-min) {
                left: calc(50% - 96px);
            }
            @media (max-width: $screen-md-min) {
                left: calc(50% - 65px);
            }
        }
    }
}

.perspectives-gallery {
    padding-bottom: 80px;
    background: linear-gradient(90deg, rgba(236, 233, 230, 0.5) 10%, #fff 90%);
    h2 {
        cursor: default;
        margin: 40px;
        text-transform: capitalize;
    }
    .paddings-1 {
        transition: all 1s ease-in-out;
        padding: 10px 0 10px 100px;
        @media (max-width: $screen-lg-min) {
            padding: 10px 0 10px 50px;
        }
        @media (max-width: $screen-md-min) {
            padding: 0;
        }
    }
    .paddings-2 {
        transition: all 1s ease-in-out;
        padding: 10px 50px 10px 50px;
        @media (max-width: $screen-lg-min) {
            padding: 10px 25px 10px 25px;
        }
        @media (max-width: $screen-md-min) {
            padding: 0;
        }
    }
    .paddings-3 {
        transition: all 1s ease-in-out;
        padding: 10px 100px 10px 0;
        @media (max-width: $screen-lg-min) {
            padding: 10px 50px 10px 0;
        }
        @media (max-width: $screen-md-min) {
            padding: 0;
        }
    }
    .grid {
        height: 100%;
        list-style: none;
        margin: 0;
        padding: 0;
        position: relative;
        text-align: center;
        width: 100%;
        @media (max-width: 800px) {
            margin: -10px 0;
        }
    }
    .grid figure {
        cursor: pointer;
        float: left;
        overflow: hidden;
        position: relative;
        text-align: center;
    }
    .grid figure img {
        display: block;
        max-width: 100%;
        min-height: 100%;
        opacity: 1;
        position: relative;
    }
    .grid figure figcaption {
        color: #fff;
        font-size: 1.25em;
        padding: 2em;
        text-transform: uppercase;
        backface-visibility: hidden;
    }
    .grid figure figcaption::before,
    .grid figure figcaption::after {
        pointer-events: none;
    }
    .grid figure figcaption,
    .grid figure figcaption > a {
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }
    .grid figure figcaption > a {
        font-size: 0;
        opacity: 0;
        text-indent: 200%;
        white-space: nowrap;
        z-index: 1000;
    }
    .grid figure h2 {
        background-color: rgba(0, 0, 181, 0.75);
        border-radius: 4px 4px 0 0 / 4px 4px 0 0;
        font-weight: 300;
        word-spacing: -0.15em;
        @media (max-width: 1600px) {
            font-size: 25px;
        }
        @media (max-width: 1460px) {
            font-size: 20px;
            text-align: center;
        }
    }
    .grid figure h2 span {
        font-weight: 800;
    }
    .grid figure h2,
    .grid figure p {
        margin: 0;
    }
    .grid figure p {
        background-color: rgba(255, 255, 255, 0.85);
        border-radius: 0 0  4px 4px / 0 0 4px 4px;
        color: #2f2f2f;
        font-size: 1em;
        @media (max-width: $screen-md-min) {
            text-align: center;
        }
    }
    /***** Lily *****/
    figure.effect-lily img {
        max-width: none;
        opacity: 1;
        transform: translate3d(-40px,0,0);
        transition: opacity 0.35s, transform 0.35s;
        width: -webkit-calc(100% + 50px);
        width: calc(100% + 50px);
    }
    figure.effect-lily figcaption {
        text-align: left;
    }
    figure.effect-lily figcaption > div {
        bottom: 0;
        height: 50%;
        left: 0;
        padding: 2em;
        position: absolute;
        width: 100%;
    }
    figure.effect-lily h2,
    figure.effect-lily p {
        transform: translate3d(0,40px,0);
    }
    figure.effect-lily h2 {
        transition: transform 0.35s;
    }
    figure.effect-lily p {
        opacity: 0;
        transition: opacity 0.2s, transform 0.35s;
    }
    figure.effect-lily:hover img,
    figure.effect-lily:hover p {
        opacity: 1;
    }
    figure.effect-lily:hover img,
    figure.effect-lily:hover h2,
    figure.effect-lily:hover p {
        transform: translate3d(0,0,0);
    }
    figure.effect-lily:hover p {
        transition-delay: 0.05s;
        transition-duration: 0.35s;
    }
    @media screen and (max-width: 50em) {
        .content {
            padding: 0 10px;
            text-align: center;
        }
        .grid figure {
            display: inline-block;
            float: none;
            margin: 0 auto;
            width: 100%;
        }
    }
}

.client-testimonials {
    margin-bottom: 80px;
    h2 {
        cursor: default;
        margin: 40px 0;
    }
    .testimls {
        border-bottom: 4px dotted rgba(250, 128, 114, 0.6);
        border-left: 4px solid rgba(250, 128, 114, 0.8);
        border-radius: 10px 10px 0 0 / 10px 10px 0 0;
        border-right: 4px solid rgba(250, 128, 114, 0.8);
        border-top: 4px solid rgba(250, 128, 114, 0.8);
        box-sizing: border-box;
        height: 150px;
        margin: 115px auto 0 auto;
        position: relative;
        transition: all 1s ease-in-out;
        width: 1000px;
        @media (max-width: 1050px) {
            width: 700px;
        }
        @media (max-width: 750px) {
            height: 200px;
            width: 400px;
        }
        @media (max-width: 400px) {
            height: 200px;
            width: 300px;
        }
        p {
            cursor: default;
            font-size: $font-size;
            margin: 0;
            padding: 90px 0 5px 0;
            text-align: center;
            text-transform: uppercase;
        }
        .hvr-rotate:hover, .hvr-rotate:focus, .hvr-rotate:active {
            transform: rotate(-35deg);
        }
        img {
            border: 10px solid rgba(250, 128, 114, 0.5);
            height: 150px;
            left: calc(50% - 75px);
            position: absolute;
            top: -75px;
            width: 150px;
        }
    }
    .testimls-2 {
        border-bottom: 4px dotted rgba(0, 150, 136, 0.6);
        border-left: 4px solid rgba(0, 150, 136, 0.8);
        border-radius: 10px 10px 0 0 / 10px 10px 0 0;
        border-right: 4px solid rgba(0, 150, 136, 0.8);
        border-top: 4px solid rgba(0, 150, 136, 0.8);
        .hvr-rotate:hover, .hvr-rotate:focus, .hvr-rotate:active {
            transform: rotate(35deg);
        }
        img {
            border: 10px solid rgba(0, 150, 136, 0.5);
        }
    }
    .testimls-3 {
        border-bottom: 4px dotted rgba(103, 58, 183, 0.6);
        border-left: 4px solid rgba(103, 58, 183, 0.8);
        border-radius: 10px 10px 0 0 / 10px 10px 0 0;
        border-right: 4px solid rgba(103, 58, 183, 0.8);
        border-top: 4px solid rgba(103, 58, 183, 0.8);
        .hvr-rotate:hover, .hvr-rotate:focus, .hvr-rotate:active {
            transform: rotate(-35deg);
        }
        img {
            border: 10px solid rgba(103, 58, 183, 0.5);
        }
    }
    .testimls-qt {
        border-bottom: 4px solid rgba(250, 128, 114, 0.8);
        border-left: 4px solid rgba(250, 128, 114, 0.8);
        border-radius: 0 0 10px 10px / 0 0 10px 10px;
        border-right: 4px solid rgba(250, 128, 114, 0.8);
        border-top: none;
        margin: 0 auto;
        text-align: center;
        transition: all 1s ease-in-out;
        width: 1000px;
        @media (max-width: 1050px) {
            width: 700px;
        }
        @media (max-width: 750px) {
            width: 400px;
        }
        @media (max-width: 400px) {
            width: 300px;
        }
        &:hover, &:focus {
            .fa {
                opacity: 1;
            }
        }
        .testimls-margin {
            padding: 20px 10px;
        }
        p {
            cursor: default;
            display: inline;
            font-size: $font-size;
            padding: 0 10px;
        }
        .fa {
            background-color: rgba(250, 128, 114, 0.5);
            border-radius: 50%;
            font-size: 20px;
            height: 50px;
            opacity: 0.7;
            padding: 5px;
            transition: all .4s ease-in-out;
            width: 50px;
            &::before {
                line-height: 42px;
            }
            @media (max-width: 400px) {
                font-size: 10px;
                background-color: transparent;
                height: 0;
                width: 0;
                &::before {
                    line-height: 0;
                }
            }
        }
    }
    .testimls-qt-2 {
        border-bottom: 4px solid rgba(0, 150, 136, 0.8);
        border-left: 4px solid rgba(0, 150, 136, 0.8);
        border-radius: 0 0 10px 10px / 0 0 10px 10px;
        border-right: 4px solid rgba(0, 150, 136, 0.8);
        .fa {
            background-color: rgba(0, 150, 136, 0.5);
            @media (max-width: 400px) {
                font-size: 10px;
                background-color: transparent;
                height: 0;
                width: 0;
                &::before {
                    line-height: 0;
                }
            }
        }
    }
    .testimls-qt-3 {
        border-bottom: 4px solid rgba(103, 58, 183, 0.8);
        border-left: 4px solid rgba(103, 58, 183, 0.8);
        border-radius: 0 0 10px 10px / 0 0 10px 10px;
        border-right: 4px solid rgba(103, 58, 183, 0.8);
        .fa {
            background-color: rgba(103, 58, 183, 0.5);
            @media (max-width: 400px) {
                font-size: 10px;
                background-color: transparent;
                height: 0;
                width: 0;
                &::before {
                    line-height: 0;
                }
            }
        }
    }
}