.page-size{
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset, 0 15px 10px -10px rgba(0, 0, 0, 0.5), 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    &:before, &:after{
        content:"";
        position:absolute;
        z-index:-2;
    }
}

.no-paddings {
    padding: 0;
}

.m-nav {
    height: 50px;
    list-style: none;
    padding: 0;
    position: absolute;
    right: 70px;
    text-transform: uppercase;
    top: 45px;
    transition: all 1s ease-in-out;
    width: 820px;
    z-index: 20;
    @media (max-width: $screen-lg-min) {
        right: 0 !important;
        left: 0;
        margin: 0 auto;
    }
    @media (max-width: 900px) {
        display: none;
    }
    li {
        background-color: rgba(55, 55, 55, 0.8);
        float: left;
        height: 50px;
        list-style: none;
        margin-right: 4px;
        padding: 0;
        position: relative;
        text-align: center;
        width: 200px;
        &:first-child {
            margin-left: 4px;
        }
        &:hover ul, &:focus ul {
            display: block;
        }
        a {
            color: #fff;
            cursor: pointer;
            display: block;
            font-size: 1.2em;
            height: 50px;
            line-height: 50px;
            text-decoration: none;
            width: 200px;
            &:focus, &:active {
                background: rgb(0, 102, 88);
            }
        }
        ul {
            display: none;
            margin: 0;
            padding: 0;
            width: 200px;
            li {
                float: none;
                height: 50px;
                margin: 0;
                text-align: center;
                width: 200px;
                &:first-child {
                    margin-left: 0;
                }
                a {
                    border-top: 1px solid #fff;
                    font-size: 1.1em;
                }
            }
        }
    }
    .display-inline {
        color: #fff;
        display: inline;
        font-size: 2em;
        left: 15px;
        line-height: 50px;
        position: absolute;
    }
}

.hvr-sweep-to-right:before {
    background-color: rgba(0, 150, 136, 0.8);
}
.burger-nav {
    position: absolute;
    top: 0;
    left: 0;
    @media (min-width: 901px) {
        display: none;
    }
}

.nav-img {
    background: url('../img/gallery/build.svg') center/cover no-repeat;
    height: 100vh;
    .caption {
        cursor: default;
        left: calc(50% - 444px);
        position: absolute;
        text-align: center;
        top: calc(50% - 104px);
        transition: all .5s ease-in-out;
        @media (max-width: $screen-lg-min) {
            left: calc(50% - 384px);
            top: calc(50% - 94px);
        }
        @media (max-width: $screen-md-min) {
            left: calc(50% - 350px);
            top: calc(50% - 88px);
        }
        @media (max-width: $screen-sm-min) {
            left: calc(50% - 293px);
            top: calc(50% - 62px);
        }
        @media (max-width: 620px) {
            left: calc(50% - 228px);
            top: calc(50% - 47px);
        }
        @media (max-width: $screen-xs-min) {
            left: calc(50% - 177px);
            top: calc(50% - 35px);
        }
        p {
            background-color: rgba(0, 150, 136, 0.8);
            border: 4px solid rgba(255, 252, 252, 0.5);
            border-radius: 20px;
            color: #fff;
            font-size: 3em;
            letter-spacing: 2px;
            margin: 0;
            padding: 40px;
            word-spacing: 6px;
            text-shadow:-1px -1px 1px rgba(255,255,255,0.2), 1px 1px 1px rgba(255,255,255,0.2), 1px 1px 1px rgba(255, 152, 0, 0.8);
            @media (max-width: $screen-lg-min) {
                font-size: 2.5em;
            }
            @media (max-width: $screen-md-min) {
                font-size: 2.2em;
            }
            @media (max-width: $screen-sm-min) {
                font-size: 1.9em;
                padding: 20px;
            }
            @media (max-width: 620px) {
                font-size: 1.4em;
                padding: 15px;
            }
            @media (max-width: $screen-xs-min) {
                font-size: 1.1em;
                padding: 10px 5px;
                border: none;
                border-radius: 0;
            }
        }
    }
}
