.container-fluid.footer-main {
    background: url('../img/gallery/footer.svg') left/cover no-repeat;
    height: 600px;
    margin: 0 0 0 -2px;
    position: relative;
    .connect-block {
        transition: all .4s ease;
        padding: 0;
        position: absolute;
        top: calc(50% - 220px);
        @media (max-width: 750px) {
            top: calc(50% - 280px);
        }
    }
    .start-prk {
        margin: 60px auto;
        text-align: center;
        p {
            color: #fff;
            cursor: default;
            font-size: 2em;
            text-transform: uppercase;
        }
        .btn {
            background-color: transparent;
            border: 2px solid #fff;
            color: #fff;
            font-size: 1.5em;
            padding: 15px;
            transition: all .4s ease;
            width: 315px;
            &:hover, &:focus {
                background-color: #fff;
                color: #009688;
                font-weight: 500;
            }
        }
    }
    .contact {
        margin: 0 auto 60px auto;
        text-align: center;
        p {
            color: #fff;
            cursor: default;
            font-size: 2em;
            text-transform: uppercase;
        }
        a {
            color: #fff;
            font-size: 2.5em;
            margin: 0 5px;
            padding: 15px;
            transition: all .2s ease;
            &:hover, &:focus, &:active {
                background-color: #fff;
                border-radius: 50%;
                color: #009688;
                cursor: pointer;
                text-decoration: none;
            }
        }
        .fa {
            height: 65px;
            width: 65px;
        }
    }
    .pos-bottom {
        border-top: 4px dotted rgba(255, 255, 255, 0.8);
        bottom: 0;
        position: absolute;
        text-align: center;
        p {
            margin: 0;
            padding: 20px 0;
            a {
                font-size: 1.2em;
                border: 1px solid transparent;
                margin: 4px;
                color: #fff;
                cursor: pointer;
                padding: 10px 10px;
                &:hover, &:focus, &:active {
                    border: 1px solid #FFFFFF;
                    border-radius: 4px;
                    text-decoration: none;
                }
                @media (max-width: $screen-xs-min) {
                    display: inline-block;
                }
            }
        }
    }
}
